import * as React from 'react';
import { Link } from 'react-router-dom';

export default () => {

  return (
    <nav className="header">
      <div className="logo"><Link to="/">HUMANIZE</Link></div>
      <div className="right">
        <ul>
          <Link to="/" ><li>Games</li></Link>
        </ul>
      </div>
    </nav>
  )
}



