import * as React from 'react';

export default (props: any) => {
  if (!props.win) { return null }

  return (
    <div className="you-win">
      <div className="winner">You won! 🎉 😁</div>
      <button onClick={props.onRestart}>Play again</button>
    </div>
  )
}



